.button {

   border-radius: 4px;
   background: linear-gradient(to right, #67b26b, #4ca2cb) !important;
   border: none;
   color: #FFFFFF;
   text-align: center;
   font-size: 22px;
   padding: 20px;
   width: 250px;
   transition: all 0.4s;
   cursor: pointer;
   margin: 5px;

}

.button span {
   cursor: pointer;
   display: inline-block;
   position: relative;
   transition: 0.4s;
 }

 .button span:after {
   content: '\00bb';
   position: absolute;
   opacity: 0;
   top: 0;
   right: -20px;
   transition: 0.5s;
 }
 .button:hover span {
   padding-right: 25px;
 }
 .button:hover span:after {
   opacity: 1;
   right: 0;
 }

